	import {mapState} from 'vuex'
	import uniIcons from '../unis/uni-icons/uni-icons.vue'
	export default{
		components:{
			uniIcons
		},
		props:{
			//是一个列数组
			data:{
				type:[Array,Object]
			}
		},
		data(){
			return{
				value:''
			}
		},
		computed:{
            ...mapState({
            	tmpId:state=>state.screen.tmpId,
            	children:state=>state.screen.children
            }),
			//checkquery中 相同列集合
			checks(){
				if(this.children && this.children.length>0){			
					return this.children.filter(v=>v.tmpId == this.tmpId)
				}			
			},
		},
		created() {
			this.value = '';
			if(this.checks && this.checks.length>0){
				this.value = this.checks[0].value;
			}
		},
		methods:{
			changeSearch(e){
				this.value = e.detail.value;	
				if(this.data && this.data.length>0){
					let children=[];
					this.data.forEach(v=>{
						children.push({
							tmpId:this.tmpId,
							columnId:v.columnId,
							operator:"like",
							value:this.value
						})
					})
					this.$store.commit("changeCondition",{
											children:children,
											value:this.value
										});	
				}else{
					uni.showToast({
						title:'无可搜索列',
						icon:'none'
					})
				}						
				
			}
		}
	}
